<template>
    <div class="bullet">
        <div class="bulletComponent" :class="n <= percen? 'isTrue' : ''" v-for="n in quantity" :key="n"></div>
    </div>
</template>
<style lang="css">
  .bullet{
    display: flex;
    width: 100%;
  }
  .bulletComponent{
    width: 30px;
    background: gray;
    height: 30px;
    margin: auto;
    border-radius: 360px;
  }
  .isTrue{
    background: blue;
  }
@media only screen and (max-width: 600px) {
  .bulletComponent{
    width: 15px;
    height: 15px;
  }
}
</style>
<script>
export default {
  props: {
    quantity: {
      type: Number,
      required: true
    },
    percen: {
      type: Number,
      required: true
    }
  }
}
</script>


